@font-face {
    font-family: 'Montserrat';
    src: 
         url('fonts/montserrat-400-normal.eot') format('eot'),
         url('fonts/montserrat-400-normal.svg') format('svg'),
         url('fonts/montserrat-400-normal.ttf') format('truetype');
    font-weight: 400;
    font-style: normal;
}
@font-face {
    font-family: 'MaterialIcons';
    src: url('fonts/MaterialIcons-Regular.woff2') format('woff2'),
         url('fonts/MaterialIcons-Regular.woff') format('woff'),
         url('fonts/MaterialIcons-Regular.eot') format('eot'),
         url('fonts/MaterialIcons-Regular.svg') format('svg'),
         url('fonts/MaterialIcons-Regular.ttf') format('truetype');
    font-weight: 400;
    font-style: normal;
}
